  width: 20vmin;
  height: 3vmin;
  animation-name: spin-20vmin-3600s;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  transform: translateX(10vmin)rotate(-90deg);
}

@keyframes spin-20vmin-3600s {
  0% {
    transform: translateX(10vmin)rotate(-90deg);
  }

  100% {
    transform: translateX(10vmin)rotate(270deg);
  }
}

